.model-page {
  position: relative;
  min-height: 45vw;
  margin-bottom: 0px !important;
  padding-bottom: 75px;
}

.model-page:before {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  z-index: -20;
  background: url(../img/bg-model-ryad.jpg) #ffffffad center top no-repeat;
  background-size: cover;
  background-blend-mode: color;
}

.model-page:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  display: block;
  position: absolute;
  display: block;
  /* background: #fff; */
  background: linear-gradient(0deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.2)), linear-gradient(198.53deg, rgba(255, 255, 255, 0.9) 12.55%, rgba(255, 255, 255, 0) 116.66%), linear-gradient(300.63deg, rgba(255, 255, 255, 0.8) 47.26%, rgba(255, 255, 255, 0) 79.54%);
  -webkit-transform: matrix(-1, 0, 0, 1, 0, 0);
  -ms-transform: matrix(-1, 0, 0, 1, 0, 0);
  transform: matrix(-1, 0, 0, 1, 0, 0);
  z-index: -10;
  top: 0;
  left: 0;
  opacity: 0.5;
  right: 0;
  bottom: 0;
}